import { MetaTags } from "react-meta-tags";
import "../../assets/css/useful.css";
import LanguageBar from "../../components/HomeComponents/topbar/languagebar";
import NavigationBar from "../../components/HomeComponents/topbar/navigationbar";
import BaseUsefulComponent from "../../components/usefulComponents/base";
import UsefullProgress from "../../components/usefulComponents/progress";
import Quicklinks from "../../components/usefulComponents/quicklinks";
import { Link } from "react-router-dom";
import HeaderComponent from "../../components/footer/headerComponent";
import { MainContext } from "../../components/contexts/MainContext";
import { useContext, useState, useEffect } from "react";
import axios from 'axios';
import { stripHtml } from "string-strip-html";
import { NumericFormat } from "react-number-format";
import { Tooltip } from "@mui/material";

const UsefulInformation = () => {

  const {
    setImages: imageModal,
    addToFavorites,
    compare,
    toggleCompare,
  } = useContext(MainContext);

  const [properties, setProperties] = useState([]);

  useEffect(() => {

    (async () => {
      const {
        data: { Properties },
      } = await axios.get("/api/qsyrys/properties");

      setProperties(
        Properties.filter(({ position }, i) => position === "second").slice(
          0,
          6
        )
      );
    })();

  }, []);

  return (
    <>
      <MetaTags>
        <title>Useful Information | Property Investment</title>
      </MetaTags>

      <div
        className="home page-template page-template-elementor_header_footer page page-id-58 wp-embed-responsive beyot-class chrome header-is-float header-sticky-fix-style elementor-default elementor-template-full-width elementor-kit-4695 elementor-page elementor-page-58 e--ua-blink e--ua-chrome e--ua-webkit elementor elementor-58"
        id="wrapper"
      >
        <LanguageBar />
        <NavigationBar />

        <div
          className="pd-top-100 pd-bottom-100 page-wrap"
          id="primary-content"
        >
          <div className="page-inner">
            <div className="elementor elementor-8461" id="8461">
              <BaseUsefulComponent />

              <UsefullProgress />

              <Quicklinks />

              <section
                className="elementor-section elementor-top-section elementor-element elementor-element-62cbd40354efb elementor-section-boxed elementor-section-height-default elementor-section-height-default"
                data-id="62cbd40354efb"
                data-element_type="section"
              >
                <div className="elementor-container elementor-column-gap-default">
                  <div
                    className="elementor-column elementor-col-100 elementor-top-column elementor-element elementor-element-f226cd4"
                    data-id="f226cd4"
                    data-element_type="column"
                  >
                    <div className="elementor-widget-wrap elementor-element-populated">
                      <div
                        className="elementor-element elementor-element-03dfb44 g5plus-heading style1 text-center elementor-widget elementor-widget-ube-heading"
                        data-id="03dfb44"
                        data-element_type="widget"
                        data-widget_type="ube-heading.default"
                      >
                        <div className="elementor-widget-container">
                          <div className="ube-heading">
                            <h6 className="ube-heading-sub-title">
                              <span></span>
                              BROSWE OUR DREAM HOUSE
                            </h6>
                            <h2 className="ube-heading-title">
                              BROWSE LATEST PROPERTIES
                            </h2>
                          </div>{" "}
                        </div>
                      </div>
                      <div
                        className="elementor-element elementor-element-13de47c elementor-widget elementor-widget-beyot-property"
                        data-id="13de47c"
                        data-element_type="widget"
                        data-widget_type="beyot-property.default"
                      >
                        <div className="elementor-widget-container">
                          <div className="ube-property">
                            <div className="ere-property-wrap">
                              <div className="ere-property clearfix property-grid  col-gap-30">
                                <div
                                  style={{
                                    columns: "unset",
                                  }}
                                  className={`property-content columns-3 columns-md-3 columns-sm-2 columns-xs-2 columns-mb-1`}
                                >
                                  {properties.map((dax, i) => {
                                    const parsedImages = JSON.parse(dax.images);

                                    const pSelectedImg =
                                      parsedImages?.[dax.selectedImg] ||
                                      parsedImages[0];

                                    const parsedStatus = JSON.parse(
                                      dax.status
                                    )[0];

                                    const [parsedPropertyType] = JSON.parse(
                                      dax.propertytype
                                    );

                                    const desc = stripHtml(dax.description);

                                    const parseDate =
                                      (new Date(dax.created_at).getTime() -
                                        new Date().getTime()) /
                                      1000 /
                                      2_592_000;

                                    let currDate = "Some time ago";

                                    if (parseDate < 1) {
                                      currDate = "Less than a month ago";
                                    } else if (parseDate < 12) {
                                      currDate = `${Math.floor(
                                        parseDate
                                      )} month(s) ago`;
                                    } else {
                                      currDate = `${Math.floor(
                                        parseDate / 12
                                      )} year(s) ago`;
                                    }

                                    return (
                                      <div
                                        key={i}
                                        className="mg-bottom-30 ere-item-wrap ere-property-featured"
                                      >
                                        <div className="property-inner">
                                          <div className="property-image">
                                            <img
                                              decoding="async"
                                              loading="lazy"
                                              width="330"
                                              height="180"
                                              src={pSelectedImg}
                                              onError={(e) => {
                                                e.target.src =
                                                  "https://www.qsyrys.com/wp-content/uploads/2017/12/no-image.jpg";
                                              }}
                                              alt={dax.name}
                                              title={dax.name}
                                            />
                                            <div className="property-action block-center">
                                              <div className="block-center-inner">
                                                <Tooltip
                                                  arrow
                                                  title={`(${parsedImages.length}) Photo(s)`}
                                                >
                                                  <div
                                                    className="property-view-gallery-wrap"
                                                    onClick={() =>
                                                      imageModal(parsedImages)
                                                    }
                                                    data-toggle="tooltip"
                                                  >
                                                    <a
                                                      data-property-id="8404"
                                                      href="javascript:void(0)"
                                                      className="property-view-gallery"
                                                    >
                                                      <i className="fa fa-camera"></i>
                                                    </a>
                                                  </div>
                                                </Tooltip>
                                                <Tooltip
                                                  arrow
                                                  title="Add to Favorites"
                                                >
                                                  <a
                                                    href="javascript:void(0)"
                                                    className="property-favorite"
                                                    data-property-id="8404"
                                                    data-toggle="tooltip"
                                                    onClick={() =>
                                                      addToFavorites(dax.id)
                                                    }
                                                    data-title-not-favorite="Add to Favorite"
                                                    data-title-favorited="It is my favorite"
                                                    data-icon-not-favorite="fa fa-star-o"
                                                    data-icon-favorited="fa fa-star"
                                                  >
                                                    <i className="fa fa-star-o"></i>
                                                  </a>
                                                </Tooltip>
                                                <Tooltip arrow title="Compare">
                                                  <a
                                                    className="compare-property"
                                                    href="javascript:void(0)"
                                                    data-property-id="8404"
                                                    data-toggle="tooltip"
                                                    onClick={() =>
                                                      toggleCompare({
                                                        id: data.id,
                                                        image: pSelectedImg,
                                                      })
                                                    }
                                                  >
                                                    <i
                                                      className={`fa ${
                                                        compare
                                                          .map((ef) => ef.id)
                                                          .includes(dax.id)
                                                          ? "fa-minus"
                                                          : "fa-plus"
                                                      }`}
                                                    ></i>
                                                  </a>
                                                </Tooltip>
                                              </div>
                                              <Link
                                                className="property-link"
                                                to={`/property/${dax.id}`}
                                                title={dax.name}
                                              ></Link>
                                            </div>

                                            {dax.position == "second" && (
                                              <div className="property-label property-featured">
                                                <p className="label-item">
                                                  <span className="property-label-bg !bg-[#624615]">
                                                    Featured{" "}
                                                    <span className="property-arrow property-arrow !border-x-[#624615]"></span>
                                                  </span>
                                                </p>
                                              </div>
                                            )}

                                            <div className="property-status">
                                              <p className="status-item">
                                                <span
                                                  className="property-status-bg"
                                                  style={{
                                                    backgroundColor:
                                                      (parsedStatus?.label)
                                                        .toLowerCase()
                                                        .indexOf("rent") != -1
                                                        ? "#32b5f8"
                                                        : "#dd3333",
                                                  }}
                                                >
                                                  {parsedStatus?.label}
                                                  <span
                                                    style={{
                                                      borderLeftColor:
                                                        (parsedStatus?.label)
                                                          .toLowerCase()
                                                          .indexOf("rent") != -1
                                                          ? "#32b5f8"
                                                          : "#dd3333",

                                                      borderRightColor:
                                                        (parsedStatus?.label)
                                                          .toLowerCase()
                                                          .indexOf("rent") != -1
                                                          ? "#32b5f8"
                                                          : "#dd3333",
                                                    }}
                                                    className={`property-arrow`}
                                                  ></span>
                                                </span>
                                              </p>
                                            </div>
                                          </div>
                                          <div className="property-item-content">
                                            <div className="property-heading">
                                              <h2 className="property-title">
                                                <a
                                                  href={`https://www.qsyrys.com/property/${dax.id}`}
                                                  title={dax.name}
                                                >
                                                  {dax.name}
                                                </a>
                                              </h2>
                                              <div className="property-price">
                                                <span>
                                                  {dax.callforprice ==
                                                  "true" ? (
                                                    "Call for price"
                                                  ) : (
                                                    <NumericFormat
                                                      thousandSeparator={true}
                                                      displayType="text"
                                                      value={Number(dax.price)}
                                                      prefix={
                                                        dax.beforepricelabel
                                                      }
                                                    />
                                                  )}
                                                </span>
                                              </div>
                                            </div>
                                            <div
                                              className="property-location"
                                              title="SYRYS INVESTMENT LTD"
                                            >
                                              <i className="fa fa-map-marker"></i>
                                              <a
                                                target="_blank"
                                                href="http://maps.google.com/?q=SYRYS%20INVESTMENT%20LTD"
                                              >
                                                <span>
                                                  SYRYS INVESTMENT LTD
                                                </span>
                                              </a>
                                            </div>
                                            <div className="property-element-inline">
                                              <div className="property-type-list">
                                                <i className="fa fa-tag"></i>
                                                <a
                                                  href={`/property-type/${parsedPropertyType?.value}`}
                                                  title={
                                                    parsedPropertyType?.value
                                                  }
                                                >
                                                  <span>
                                                    {parsedPropertyType?.label}{" "}
                                                  </span>
                                                </a>
                                              </div>
                                              <div className="property-agent">
                                                <div
                                                  href="/author/syrysgmail-com/"
                                                  title="SYRYS GROUP"
                                                >
                                                  {" "}
                                                  <i className="fa fa-user"></i>
                                                  <span>SYRYS GROUP</span>
                                                </div>{" "}
                                              </div>
                                              <div className={`property-date`}>
                                                <>
                                                  <i className="fa fa-calendar"></i>
                                                  {currDate}
                                                </>
                                              </div>
                                            </div>
                                            <div className={`property-excerpt`}>
                                              <p>
                                                {desc.result.substring(0, 800)}{" "}
                                                []
                                              </p>
                                            </div>
                                            <div className="property-info">
                                              <div className="property-info-inner">
                                                <div className="property-area">
                                                  <div
                                                    className="property-area-inner property-info-item-tooltip"
                                                    data-toggle="tooltip"
                                                    title="Size"
                                                  >
                                                    <span className="fa fa-arrows"></span>
                                                    <span className="property-info-value">
                                                      {dax.area} m<sup>2</sup>{" "}
                                                    </span>
                                                  </div>
                                                </div>
                                                <div className="property-bedrooms">
                                                  <div
                                                    className="property-bedrooms-inner property-info-item-tooltip"
                                                    data-toggle="tooltip"
                                                    title={`${dax.bedrooms} Bedrooms`}
                                                  >
                                                    <span className="fa fa-hotel"></span>
                                                    <span className="property-info-value">
                                                      {dax.bedrooms}
                                                    </span>
                                                  </div>
                                                </div>
                                                <div className="property-bathrooms">
                                                  <div
                                                    className="property-bathrooms-inner property-info-item-tooltip"
                                                    data-toggle="tooltip"
                                                    title={`${dax.bathrooms} Bathrooms`}
                                                  >
                                                    <span className="fa fa-bath"></span>
                                                    <span className="property-info-value">
                                                      {dax.bathrooms}
                                                    </span>
                                                  </div>
                                                </div>
                                              </div>
                                            </div>
                                          </div>
                                        </div>
                                      </div>
                                    );
                                  })}
                                </div>
                                <div className="clearfix"></div>
                              </div>
                            </div>
                          </div>
                        </div>
                      </div>
                    </div>
                  </div>
                </div>
              </section>
            </div>
          </div>
        </div>
        <HeaderComponent />
      </div>
    </>
  );
};

export default UsefulInformation;
